@import url(https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap);
*{margin:0;padding:0;outline:0;box-sizing:border-box}html,body,#root{height:100%;background-color:#333}html .app,body .app,#root .app{font-family:Roboto, sans-serif}

.buttonsContainer{display:flex;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:0 3px;margin-bottom:5px}.buttonsContainer button{display:flex;justify-content:center;width:100%;height:45px;color:#fff;border:none;border-radius:4px;font-family:Roboto;font-size:20px;margin:5px;padding:0}.buttonsContainer button.finishButton{background-color:#009CDD}.buttonsContainer button.finishButton:hover,.buttonsContainer button.finishButton:focus{background-color:#0086c0}.buttonsContainer button.returnButton{background-color:#f10}.buttonsContainer button.returnButton:hover,.buttonsContainer button.returnButton:focus{background-color:#ca0000}@media screen and (max-width: 768px){.buttonsContainer button{height:65px;font-size:24px}}

.gallery{display:flex;flex-direction:column;align-items:center;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:16px}.gallery .photoContainer{position:relative;display:flex;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin-bottom:16px;box-shadow:0 0 10px #000}.gallery .photoContainer .closeButton{display:flex;flex-direction:column;justify-content:center;align-items:center;width:30px;height:30px;position:absolute;top:0;right:0;background-color:red;border-radius:50%;margin:5px}.gallery .photoContainer .closeButton span{display:block;background-color:#fff;width:20px;height:2px}.gallery .photoContainer .closeButton span:nth-child(1){-webkit-transform:rotate(45deg);transform:rotate(45deg);position:absolute}.gallery .photoContainer .closeButton span:nth-child(2){-webkit-transform:rotate(-45deg);transform:rotate(-45deg);position:absolute}.gallery .photoContainer img{max-width:100%;height:auto}

.errorScreen{display:flex;justify-content:center;align-items:center;position:fixed;top:0;right:0;bottom:0;left:0;background-color:#212121;z-index:2;display:none}.errorScreen span{color:#fff;font-size:20px;line-height:30px;max-width:80%;text-align:center}

